<template>
    <div class="portfolio">
        <div 
            class="portfolio__container" 
            v-for="(p, index) in portfolio" :key="index" 
            :class="[p.module == 'Skills' ? 'portfolio__container--skills' : 'portfolio__container--projects']"
        >
            <div class="portfolio__wrap">
                <div class="portfolio__header">
                    <h2>{{ p.module }}</h2>
                </div>
                <div class="portfolio__body">
                    <div 
                        class="portfolio__content" 
                        v-for="(tech, i) in p.technologies" 
                        :key="i"
                    >
                        <div 
                            class="portfolio__icon" 
                            v-if="tech.icon != null" 
                            v-html="tech.icon"
                        ></div>
                        <div class="portfolio__title">{{ tech.name }}</div>
                        <!-- <div class="portfolio__description">{{ tech.description }}</div> -->
                    </div>
                </div>
            </div>
        </div>
        <div class="portfolio__box"></div>
    </div>
</template>

<script>
import portfolioContent from '@/mixins/portfolioContent';
export default {
    mixins: [portfolioContent],
}
</script>

<style lang="scss" scoped>
    .portfolio {
        @include flex($justify: center, $align: center);
        width: 100%;
        height: 100%;
        &:after {
            @include position($position: absolute, $top: 0, $left: 0);
            width: 100%;
            height: 100%;
            background-color: $white;
            z-index: -1;
        }
        &__container {
            height:calc(100% - 80px);
            width:calc(100% - 80px);
            &--skills {
                @include sm {
                   padding-top: 300px;
                }
                .portfolio__content {
                    background-color: $box-bg-sub;
                }
            }
            &--projects {
                .portfolio__wrap {
                    z-index: 0 !important;
                }
                .portfolio__title {
                    color: $black;
                    @include sm {
                        font-size: .9rem;
                    }
                    @include md {
                        font-size: .9rem;
                    }
                    @include lg {
                        font-size: 1rem;
                    }
                    @include xl {
                        font-size: 1.2rem;
                    }
                }
            }
        }
        &__wrap {
            position: relative;
            z-index: 1;
            height: 100%;
            width: 100%;
            @include flex($justify: center, $align: center);
            flex-direction: column;
        }
        &__header {
            h2 {
                color: $black;
                text-shadow: -2px 2px 0px #999999a6;
            }
        }
        &__body {
            @include flex($justify: center, $align: center);
            flex-wrap: wrap;
        }
        &__content {
            @include sm {
                flex: 0 0 calc(100%);
            }
            @include md {
                flex: 0 0 calc(50%);
            }
            @include lg {
                flex: 0 0 calc(50%);
            }
            @include xl {
                flex: 0 0 calc(33.33%);
            }
            @include flex($justify: center, $align: center);
            flex-direction: column;
            padding: 10px;
            text-align: center;
        }
        &__icon {
            font-size: 3rem;
            color: $black;
        }
        &__title {
            font-size: 1rem;
            color: $black;
        }
        &__box {
            @include position($position: absolute, $top: 0, $left: 0);
            background-color: $box-bg-sub;
            animation: spread 1s;
            width: 50%;
            height: 100%;
        }
        @keyframes spread {
            0% {
                width: 100%;
                height: 100%;
            }
            100% {
                width: 50%;
                height: 100%;
            }
        }
    }
</style>